import React from "react";
import { Link } from "react-router-dom";

function Navbar() {
    return (
        <nav className="navbar bg-light navbar-light navbar-expand-sm fixed-top">
            <div className="container">

                <div className="navbar-brand">Lucas Major</div>

                <button className="navbar-toggler" type="button"
                    data-toggle="collapse" data-target="#toggleNav"
                    aria-controls="toggleNav"
                    aria-expanded="false" aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon"></span>
                </button>

                <div className="collapse navbar-collapse" id="toggleNav">
                    <div className="navbar-nav mx-auto">
                        <Link to="/">
                            <li className="nav-item nav-link">Home</li>
                        </Link>
                        <Link to="/resume">
                            <li className="nav-item nav-link">Resume</li>
                        </Link>
                        <Link to="/projects">
                            <li className="nav-item nav-link">Projects</li>
                        </Link>
                    </div>
                </div>
                <span className="navbar-text d-none d-sm-inline-block">Software Engineer</span>
            </div>
        </nav>
    );
}

export default Navbar;