import React from 'react';
import { Document, Page, pdfjs } from 'react-pdf';
import resumePDF from "../images/updated_resume.pdf";
import "../styles/Resume.css"
pdfjs.GlobalWorkerOptions.workerSrc = `//cdnjs.cloudflare.com/ajax/libs/pdf.js/${pdfjs.version}/pdf.worker.js`;




function Resume() {

    return (
        <div className="container mt-5">
            {/*DESKTOP |medium breakpoint| DISPLAY*/}
            <div className="mt-5 text-center extra-margin d-none d-sm-block">
                <div className="display-4 extra-margin">Download A Copy</div>
                <a href={resumePDF} download="Lucas_Major_Resume">PDF Download</a>
                <div className="row mx-auto">
                    <div className="resume-container col-md-8 mx-auto">
                        <Document file={resumePDF}>
                            <Page pageNumber={1} />
                        </Document>
                    </div>
                </div>
            </div>
            {/*MOBILE |small breakpoint| DISPLAY*/}
            <div className="mt-5 text-center d-block d-sm-none">
                <h3 className="extra-margin">Download A Copy</h3>
                <a href={resumePDF} download="Lucas_Major_Resume">PDF Download</a>
                {/* <div className="row mx-auto">
                    <div className="resume-container col-md-8 mx-auto">
                        <Document file={resumePDF}>
                            <Page pageNumber={1} />
                        </Document>
                    </div>
                </div> */}
            </div>
        </div>
    );
}

export default Resume;