import React, { useState } from "react";
import me from "../images/me.jpg"
import "../styles/Projects.css"
import SingleProject from "./SingleProject";

function Projects() {

    const [projectInfo] = useState([
        {
            id: "project-1",
            title: "Todo List",
            repoLink: "https://github.com/Lumajor/to-do-list",
            demoLink: "https://lumajor.github.io/to-do-list/",
            technologies: [
                {name: "Javascript"},
                {name: "HTML/CSS"},
                {name: "Git"}
            ],
            description: "Project aimed at following SOLID principles, utilizing IFFE's and Factory Functions"

        },
        {
            id: "project-2",
            title: "Library App",
            repoLink: "https://github.com/Lumajor/Library",
            demoLink: "https://lumajor.github.io/Library/",
            technologies: [
                {name: "Javascript"},
                {name: "HTML/CSS"},
                {name: "Git"},
                {name: "Firebase"}
            ],
            description: "experienced working with promises/asynchronous code, working with FireBase integration/API"
        },
        {
            id: "project-3",
            title: "Memorization Game",
            repoLink: "https://github.com/Lumajor/memorization-game",
            demoLink: "https://lumajor.github.io/memorization-game/",
            technologies: [
                {name: "React"},
                {name: "HTML/CSS"},
                {name: "Git"}
            ],
            description: "Uses React to manage state throughout the application. Utilization of props, JSX, Hooks, Functional Components"
        },
        {
            id: "project-4",
            title: "Shopping Cart",
            repoLink: "https://github.com/Lumajor/shopping-cart",
            demoLink: "https://lumajor.github.io/shopping-cart/",
            technologies: [
                {name: "React"},
                {name: "HTML/CSS"},
                {name: "Git"}
            ],
            description: "Uses React to manage state throughout the application. Utilization of React Router, props, JSX, Hooks, Functional Components"
        }
    ])

    return (
        <div className="container mt-5">
            {/*DESKTOP |medium breakpoint| DISPLAY*/}
            <div className="display-3 text-center mb-5 d-none d-sm-block extra-margin">Projects
                <a href="https://github.com/Lumajor"><img src={me} className="rounded-circle ml-5" alt="Github Repo"></img></a>
            </div>
            {/*MOBILE |small breakpoint| DISPLAY*/}
            <div className="display-4 text-center mb-5 d-block d-sm-none extra-margin">Projects
                <a href="https://github.com/Lumajor"><img src={me} className="rounded-circle ml-5" alt="Github Repo"></img></a>
            </div>
                {projectInfo.map((project) => (
                    <SingleProject project={project} key={project.id}/>
                ))}
        </div>
    );
}

export default Projects;