import React from "react";
import "../styles/SingleProject.css"

function SingleProject(props) {

    const { project } = props

    return (
        <div className="figure-container mb-5">
            <div className="display-4 w-100 project-title d-none d-sm-block hvr-grow">{project.title}</div>
            <div className="display-4 w-100 project-title d-block d-sm-none text-center">{project.title}</div>
            {/*DESKTOP |medium breakpoint| DISPLAY*/}
            <div className="row mt-3 align-items-center d-none d-sm-flex hvr-grow">
                <section className="col-4 my-auto ">
                    <a href={project.repoLink}>Github Repo</a><br/>
                    <a href={project.demoLink}>Demo</a>
                </section>
                <section className="col-4">
                    <h5>Technology Used</h5>
                        <div>
                            {project.technologies.map((technology) => (
                                <span class="badge badge-primary mx-2">{technology.name}</span>
                            ))}
                        </div>
                </section>
                <section className="col-4">
                    <h5>Description</h5>
                        <div> 
                            {project.description}
                        </div>
                </section>
            </div>
            
            {/*MOBILE |small breakpoint| DISPLAY*/}
            <div className="row mt-3 align-items-center d-block d-sm-none">
                <section className="text-center">
                    <a href={project.repoLink}>Github Repo</a><br/>
                    <a href={project.demoLink}>Demo</a>
                </section>
                <section className="mt-3 text-center">
                    <h5>Technology Used</h5>
                        <div>
                            {project.technologies.map((technology) => (
                                <span class="badge badge-primary mx-2">{technology.name}</span>
                            ))}
                        </div>
                </section>
                <section className="mt-3 text-center">
                    <h5>Description</h5>
                        <div> 
                            {project.description}
                        </div>
                </section>
            </div>
        </div>
    );
}

export default SingleProject;